.clock {
  display: flex;
  align-items: center;
  justify-content: center;
  width: $clockSize;
  height: $clockSize;

  border-radius: $circle;
  background-color: #5b6264;
  animation: shape 5s linear 0s infinite alternate;

  &__middle {
    width: 50px;
    height: 50px;

    border-radius: $circle;
    background-color: #2c2a2a;
  }

}
